import React from "react";
import { NavLink } from "react-router-dom";
import { skillData } from "../data/skillData";
import Typewriter from "typewriter-effect";
import { motion } from "framer-motion";
// import IconsDisplay from "./IconsDisplay";
const Header = () => {
  skillData.map((skill) => console.log(skill));
  return (
    <div className="header">
      {/* <IconsDisplay /> */}
      <div className="content">
        <motion.h1
          drag
          onDragEnd
          dragConstraints={{
            left: -250,
            right: 0,
            top: -50,
            bottom: 350,
          }}
        >
          {/* <span>Je suis Ange Elisée Kouassi,</span>
          <br />
          <span>Développeur web_</span> */}
          <Typewriter
            options={{
              strings: [
                "Je suis Ange Elisée Kouassi,",
                "Je suis développeur web,",
              ],
              autoStart: true,
              pauseFor: 4500,
              delay: 120,
              loop: true,
              cursor: "<strong style='color: #A90732;'>_</strong>",
              skipAddStyles: false,
            }}
          />
        </motion.h1>
        <h2>développeur Front-end_</h2>
        <p>
          <i class="fa-solid fa-check"></i> J'ai aussi une bonne connaissance en
          Back-end_
        </p>
        <div className="btn">
          <ul>
            <a
              href="http://localhost:3000/"
              target="_blank"
              rel="noopener noreferrer"
            >
              <li>voir mon CV</li>
            </a>
            <NavLink to="/contact">
              <li>me contacter</li>
            </NavLink>
          </ul>
        </div>
      </div>
    </div>
  );
};

export default Header;
