import React from "react";
import { NavLink } from "react-router-dom";
import Logo from "./Logo";
import SocialNetwork from "./SocialNetwork";

const Navigation = () => {
  return (
    <div className="navigation">
      <nav>
        <div className="name">
          <h3>
            <Logo />
          </h3>
        </div>
        <ul className="nav-item">
          <NavLink
            to="/competences"
            className={(nav) => (nav.isActive ? "nav-active" : "")}
          >
            <li>Compétences</li>
          </NavLink>
          <NavLink
            to="/portfolio"
            className={(nav) => (nav.isActive ? "nav-active" : "")}
          >
            <li>Portfolio</li>
          </NavLink>
          <NavLink
            to="/about"
            className={(nav) => (nav.isActive ? "nav-active" : "")}
          >
            <li>A propos</li>
          </NavLink>
          <NavLink
            to="/contact"
            className={(nav) => (nav.isActive ? "nav-active" : "")}
          >
            <li>Contact</li>
          </NavLink>
        </ul>
        <SocialNetwork />
      </nav>
    </div>
  );
};

export default Navigation;
